@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white;
}
.heading{
  text-align: center;
  margin: 10px 0 10px 0;
}
.searchbar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 1.25rem;
  margin-bottom: 1.25rem;
}

.searchbar .searchbar__input {
  border: none;
  border-radius: 0.625rem;
  background-color: lightblue;
  
  font-size: 1rem;
  padding-left: 0.625rem;
  padding-top: 0.625rem;
  padding-bottom: 0.625rem;
  width: 70vw;
  height: 30vm;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.2);
  outline: none;
}

.searchbar .searchbar__input::-webkit-input-placeholder {
  color: lightblue;
}

.searchbar .searchbar__input:-ms-input-placeholder {
  color: lightblue;
}

.searchbar .searchbar__input::placeholder {
  color: black;
}

.searchbar .searchbar__input:hover {
  box-shadow: none;
}

.searchbar .searchbar__input:focus {
  opacity: 0.9;
  box-shadow: none;
}

.searchbar .searchbar__button {
  border: none;
  border-radius: 0.625rem;
  background-color:blue;
  color: var(--color-text-button);
  margin-left: 4px;
  padding: 0.625rem;
  font-size: 1rem;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.2);
}

.searchbar .searchbar__button:hover {
  box-shadow: none;
}

.searchbar .searchbar__button:active {
  opacity: 0.9;
}

.snippet {
  display: -webkit-flex;
  display: flex;
  max-width: 80vw;
  margin: auto;
  margin-bottom: 0.75rem;
  padding-left: 1.25rem;
  padding-right: 0.75rem;
  background-color: lightblue;
  border-radius: 0.625rem;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.2);
}

.snippet .snippet__title {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
}

.snippet .snippet__cover {
  margin-top: 0.375rem;
  margin-bottom: 0.375rem;
  -webkit-flex-grow: 0.4;
          flex-grow: 0.4;
  border-radius: 5px;
}

.snippet .snippet__image {
  border-radius: 5px;
  display: block;
  margin: auto;
  width: 100px;
}

.snippet .snippet__book-title {
  color: black;
  font-size: 1.125rem;
  margin-bottom: 0.25rem;
}

.snippet .snippet__book-author {
  color: black;
}

.snippet:hover {
  box-shadow: none;
}

.load-more-button {
  border: none;
  border-radius: 0.625rem;
  background-color: rgb(110, 128, 143);
  color: black;
  display: none;
  margin: auto;
  margin-bottom: 1rem;
  padding: 0.625rem;
  font-size: 1rem;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.2);
}

.load-more-button:hover {
  box-shadow: none;
}

.load-more-button:active {
  opacity: 0.9;
}

@media (min-width: 900px) {
  #Snippet {
    width: 60vw;
  }
}
