.modal {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.2);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s;
}

.modal .modal__content {
  background-color: lightblue;
  min-width: 70vw;
  height: 70vh;
  border-radius: 10px;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  padding-top: 1.25rem;
}

.modal .modal__cover {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  border-radius: 5px;
}

.modal .modal__image {
  border-radius: 5px;
  width: 200px;
}

.modal .modal__info p {
  margin-bottom: 0.25rem;
}

.modal .modal__book-title {
  color: black;
  font-size: 1.125rem;
  margin-bottom: 0.25rem;
}

.modal .modal__book-info {
  color: black;
}

.modal.active {
  opacity: 1;
  pointer-events: all;
}

@media (min-width: 900px) {
  .modal__content {
    min-width: 50vw;
  }
}
/*# sourceMappingURL=modal.css.map */
